import React, { ReactNode } from 'react'
import * as styles from './section.module.css'

export interface SectionProps {
    children: ReactNode,
    className?: string,
    backgroundColor?: string,
    backgroundImage?: string,
}


export default function Section({ backgroundColor, backgroundImage, children, className, backgroundMode }: SectionProps) {
    let inlineStyle: any = {}
    if (backgroundColor) inlineStyle.backgroundColor = backgroundColor
    if (backgroundImage) inlineStyle.backgroundImage = `url(${backgroundImage})`

    return (
        <div className={styles.section} style={backgroundMode !== 'internal' ? inlineStyle : undefined}>
            <section className={`${className}`}>
                { children }
            </section>
        </div>
    )
}
