import React from 'react';
import Page from '../components/Page';
import Metadata from '../components/Metadata';
import Banner from '../components/banner';
import { BreadCrumbs } from 'src/components/BreadCrumbs';
import Article from '../components/article';
import Section from '../components/section';
import { BackgroundImageGeneric } from '../components/generic/BackgroundImageGeneric';
import { graphql, useStaticQuery } from 'gatsby';
import * as styles from '../pages/styling/vgw-play-ferrari.module.css';

export const VGWPlayFerrariPage = () => {
  const images = useStaticQuery(graphql`
    query {
      heroDesktop: file(relativePath: { eq: "hero/vgw-play-ferrari-hero-banner-desktop.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 4160) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      heroTablet: file(relativePath: { eq: "hero/vgw-play-ferrari-hero-banner-tablet.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 1024) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      heroMobile: file(relativePath: { eq: "hero/vgw-play-ferrari-hero-banner-mobile.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 490) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Page>
      <Metadata
        title='VGW Play Partners | Global Poker'
        description='Global Poker’s parent company VGW is pleased to announce a multi-year agreement with Scuderia Ferrari as a Premium Partner of the Formula 1 team.'
      />
      <VGWPlayFerrariPageContent images={images} />
    </Page>
  );
};

const VGWPlayFerrariPageContent: React.FC<any> = ({ images }) => {
  const sources = [
    images.heroMobile.childImageSharp.fluid,
    { ...images.heroTablet.childImageSharp.fluid, media: `(min-width: 490px)` },
    { ...images.heroDesktop.childImageSharp.fluid, media: `(min-width: 1024px)` },
  ];

  const breadcrumbs = [
    { to: '/', text: 'Home' },
    { to: '/vgw-play-ferrari', text: 'FERRARI X VGW PLAY' },
  ];

  return (
    <>
      <Banner sources={sources} title=''></Banner>
      <Article>
        <section className={styles.allInFerrariSection}>
          <div className={styles.contentBannerBreadcrumbs}>
            <BreadCrumbs items={breadcrumbs} />
          </div>
          <div className={styles.ferrariContent}>
            <h2 className={styles.whiteText}>ALL IN ON FERRARI</h2>
            <p className={`${styles.whiteText} ${styles.ferrariParagraphText}`}>
              We have exciting news! Global Poker’s parent company VGW is pleased to announce a multi-year agreement with Scuderia Ferrari as a
              Premium Partner of the Formula 1 team. 
            </p>
            <p className={`${styles.whiteText} ${styles.ferrariParagraphText}`}>
              This dynamic partnership brings together two global brands, each with a shared passion for harnessing the power of technology,
              innovation and teamwork to entertain and drive world-leading customer experience and success.
            </p>
          </div>
        </section>

        <section className={styles.ferrariTeamSection}>
          <img className={styles.ferrariTeamImage1} src='/images/vgw-play-ferrari/content-02.jpg' />
          <div className={styles.ferrariContentWrapper}>
            <div className={styles.ferrariTeamContent}>
              <p className={styles.ferrariParagraphText}>
                The VGW team wishes Charles, Carlos and the entire crew at Scuderia Ferrari all the very best for the 2023 Formula 1 Championship
                season! <br />
              </p>
              <p className={styles.ferrariParagraphText}>
                That’s not all; as part of the partnership between VGW and Scuderia Ferrari, our very own Global Poker will feature on the Ferrari
                race car in each Formula 1 race, in both USA and Canada.
              </p>
            </div>
          </div>
        </section>

        <section className={styles.ferrariVideoSection}>
          <div className={styles.ferrariVideoWrapper}>
            <iframe
              src='https://player.vimeo.com/video/850785978?badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479'
              allow='autoplay; fullscreen; picture-in-picture'
              style={{
                position: 'absolute',
                background: 'black',
                display: 'block',
                top: '0',
                left: '0',
                width: '100%',
                height: '100%',
                border: '0px',
              }}
              title='VGW Play + Ferrari Premium Partner'
            ></iframe>
          </div>
          <div className={styles.ferrariImageWrapper}>
            <div className={styles.ferrariImageContainer1}>
              <div className={styles.ferrariInnerImageContainer}>
                <img src='/images/vgw-play-ferrari/content-04.jpg' />
                <img src='/images/vgw-play-ferrari/content-05.jpg' />
              </div>
              <img src='/images/vgw-play-ferrari/content-03.jpg' />
            </div>
            <div className={styles.ferrariImageContainer2}>
              <img src='/images/vgw-play-ferrari/content-06.jpg' />
              <img src='/images/vgw-play-ferrari/content-07.jpg' />
              <img src='/images/vgw-play-ferrari/content-08.jpg' />
            </div>
          </div>
        </section>
      </Article>
    </>
  );
};

export default VGWPlayFerrariPage;
