import React, {ReactNode, useEffect} from 'react'
import * as styles from './banner.module.css'
import { BackgroundImageGeneric } from '../generic/BackgroundImageGeneric';
import Button, {ButtonProps} from '../button';

export interface BannerProps {
    sources?: any,
    image?: any,
    title?: string,
    subtitle?: string,
    subtitle2?: string,
    subtitleDate?: string,
    children?: ReactNode,
    button?: ButtonProps,
    color?: string,
    textShadow?: string,
    width?: string,
    height? :string,
    marginLeft?: string,
    marginRight?: string,
    alignToTopForMobile?: boolean,
    className?: string
}

function Banner(
{
    sources,
    image,
    title,
    subtitle,
    subtitle2,
    subtitleDate,
    children,
    button,
    color,
    textShadow,
    width='800px',
    height,
    marginLeft,
    marginRight,
    alignToTopForMobile=true,
    className
}: BannerProps) {
    const MINIMAL_WIDTH_TO_ENABLE_MARGIN_SUPPORT=1024
    const HEADER_TEST_PADDING_OFFSET=20
    const [dimensions, setDimensions] = React.useState({
        height: 0,
        width: 0
    })

    useEffect(() => {
        function handleResize() {
            const windowGlobal = typeof window !== 'undefined' && window;
            setDimensions({
                height: windowGlobal.innerHeight,
                width: windowGlobal.innerWidth
            })
        }
        handleResize()
        window.addEventListener('resize', handleResize)
        return () => {
            window.removeEventListener('resize', handleResize)
        }
    }, [])

    function getHeaderColorAndShadowing() {
        let style = {
            color: '#FFFFFF',
            textShadow: '2px 2px 0px #000000'
        }
        if (color) {
            style.color = color
            style.textShadow = '0px 0px 0px'
        }
        if (textShadow) style.textShadow=`2px 5px 5px ${textShadow}`
        return style
    }

    function getHeightStyle(){
        let style = { height: '500px' }
        if (height) style.height = height
        return style
    }

    function customWidthCanBeSet() {
        return width && parseInt(width, 10) + HEADER_TEST_PADDING_OFFSET < dimensions.width
    }

    function getWidthStyle() {
        let style = {}
        if (customWidthCanBeSet()) style.width = width
        return style
    }

    function getMarginsStyle() {
        let style = {}
        if (dimensions.width >= MINIMAL_WIDTH_TO_ENABLE_MARGIN_SUPPORT) {
            if (marginLeft) {
                style.marginRight = 'auto'
                style.marginLeft = marginLeft
            }
            if (marginRight) {
                style.marginLeft = 'auto'
                style.marginRight = marginRight
            }
        }
        return style
    }

    function getImageSource() {
        if (sources) return sources
        else return [image]
    }

    function getCustomClasses() {
        if (alignToTopForMobile) return styles.alignToTopForMobile
    }

    function renderDefaultHeroBanner() {
        const inlineColor = getHeaderColorAndShadowing()
        const inlineWidth = getWidthStyle()
        const inlineMargins = getMarginsStyle()
        const Subtitle = subtitle && <h2 className={styles.bannerHeading2} style={inlineColor}>{subtitle}</h2>
        const Subtitle2 = subtitle2 && <h2 className={styles.bannerHeading2} style={inlineColor}>{subtitle2}</h2>
        const SubtitleDate = subtitleDate && <p style={inlineColor} className={styles.date}>{subtitleDate}</p>
        const HeroButton = button && <div><Button url={button.url} label={button.label} color={button.color} type='hero' /></div>
        return (
            <div style={{...inlineWidth, ...inlineMargins}} className={styles.content}>
                <h1 className={styles.bannerHeading1} style={inlineColor}>{title}</h1>
                {Subtitle}
                {Subtitle2}
                {SubtitleDate}
                {HeroButton}
            </div>
        )
    }

    return (
        <BackgroundImageGeneric
            className={`${styles.banner} ${getCustomClasses()} ${className}`}
            fluid={getImageSource()}
            style={getHeightStyle()}
        >
            { children ? children : renderDefaultHeroBanner() }
        </BackgroundImageGeneric>
    )
}

export default Banner
