import React, { ReactNode } from 'react'
import * as styles from './article.module.css'
import Section from '../section';
import {BreadCrumbs} from '../BreadCrumbs';

interface ArticleProps {
    children: ReactNode,
    backgroundColor?: string,
    backgroundImage?: string,
    path?: {
        to: string,
        text: string
    }[]
}

export default function Article({ children, backgroundColor, backgroundImage, path }: ArticleProps) {
    let inlineStyle: any = {}
    if (backgroundColor) inlineStyle.backgroundColor = backgroundColor
    if (backgroundImage) inlineStyle.backgroundImage = `url(${backgroundImage})`

    const breadcrumbs = path && (
        <Section>
            <BreadCrumbs items={path} />
        </Section>
    )

    return (
        <main>
            <article className={styles.article} style={inlineStyle}>
                {breadcrumbs}
                {children}
            </article>
        </main>
    )
}
