import React from 'react';
import BackgroundImage from 'gatsby-background-image';
import { convertToBgImage } from 'gbimage-bridge';
import { getImage } from 'gatsby-plugin-image';

interface BackgroundImageGenericProps {
  img?: any;
  children?: any;
  className?: string;
  fluid?: any[];
  style? :string;
  alt? :string;
}

export const BackgroundImageGeneric: React.FC<BackgroundImageGenericProps> = (props) => {

  const image = convertToBgImage(getImage(props.img));
  return (
    <BackgroundImage fluid={props.fluid} className={props.className} {...image} alt={props.alt} style={props.style}>
      {props.children}
    </BackgroundImage>
  );
};
