import React from 'react';
import * as styles from './breadcrumbs.module.css';
import Link from 'src/components/Link';

export const BreadCrumbs = ({ items }: { items: { to: string; text: string }[] }) => (
  <ul className={styles.index}>
    {items.map(i => (
      <li key={i.to}>
        <Link className={styles.breadCrumbLink} to={i.to}>
          {i.text}
        </Link>
      </li>
    ))}
  </ul>
);
